import axios from "axios";
import React, { PureComponent } from "react";

import "./header.css";

export default class Header extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { stats: "" };
  }

  componentDidMount() {
    axios.get("https://mohammedfarish-public-api.herokuapp.com/v1/ping")
      .then((response) => {
        const { status } = response.data;
        this.setState({
          stats: status,
        });
      })
      .catch(() => {
        this.setState({
          stats: "ok",
        });
      });
  }

  render() {
    const { stats } = this.state;
    return (
      <div className="header">
        {stats}
      </div>
    );
  }
}
